	module.exports = {"data":{"challengeNode":{"title":"Create a Media Query","description":["媒体查询是 CSS3 中引入的一项新技术，它可以根据不同的可视窗口大小显示不同的布局。可视窗口是用户在网页上的可见区域，根据访问网站的设备不同而不同。","媒体查询由媒体类型组成，如果媒体类型与展示网页的设备类型匹配，则应用对应的样式。你可以在媒体查询中用上你想用的选择器和样式。","下面是一个媒体查询的例子，当设备宽度小于或等于 100px 时返回内容：","<code>@media (max-width: 100px) { /* CSS Rules */ }</code>","以下定义的媒体查询，是当设备高度大于或等于 350px 时返回内容：","<code>@media (min-height: 350px) { /* CSS Rules */ }</code>","只有当媒体类型与当前设备匹配时，才应用媒体查询中的 CSS。","<hr>","增加一个设备的高度小于或等于 800px 时，<code>p</code> 标签的 <code>font-size</code> 为 12px 的媒体查询。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/responsive-web-design-principles/create-a-media-query","blockName":"Responsive Web Design Principles","tests":[{"text":"当设备 <code>height</code> 小于或等于 800px 时，<code>p</code> 元素 <code>font-size</code> 应为 12px。","testString":"assert($('p').css('font-size') == '12px', '当设备 <code>height</code> 小于或等于 800px 时，<code>p</code> 元素 <code>font-size</code> 应为 12px。');"},{"text":"使用 <code>@media</code> 为 <code>height</code> 小于或等于 800px 的设备添加一个媒体查询。","testString":"assert(code.match(/@media\\s?\\(max-height:\\s*?800px\\)/g), '使用 <code>@media</code> 为 <code>height</code> 小于或等于 800px 的设备添加一个媒体查询。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  p {\n    font-size: 20px;\n  }\n  \n  /* Add media query below */\n  \n</style>\n  \n<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Vivamus quis tempus massa. Aenean erat nisl, gravida vel vestibulum cursus, interdum sit amet lectus. Sed sit amet quam nibh. Suspendisse quis tincidunt nulla. In hac habitasse platea dictumst. Ut sit amet pretium nisl. Vivamus vel mi sem. Aenean sit amet consectetur sem. Suspendisse pretium, purus et gravida consequat, nunc ligula ultricies diam, at aliquet velit libero a dui.</p>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/responsive-web-design-principles/make-an-image-responsive","id":"587d78b0367417b2b2512b08"},"slug":"/responsive-web-design/responsive-web-design-principles/create-a-media-query"}}

